<template>
  <div>
    <div class="page-title">
      <h3>Личная статистика игрока - {{ $route.params.nick }}</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <p>Выберите период:</p>
      <select style="display: block" v-model="choosed" ref="sl">
        <option v-for="d of period" :key="d.id">{{ d.title }}</option>
      </select>
      <Loader v-if="loading" />
      <div v-else>
        <p>Общая статистика:</p>
        <div v-if="n.length < 1">Игр не найдено.</div>
        <v-table :data="n" v-else class="responsive-table highlight">
          <thead slot="head">
            <v-th sortKey="playerName" defaultSort="desc">Ник</v-th>
            <v-th sortKey="totalPlayedGames" defaultSort="desc"
              >Кол-во игр</v-th
            >
            <v-th sortKey="kills" defaultSort="desc">Убийств</v-th>
            <v-th sortKey="teamkills" defaultSort="desc">Тимкиллов</v-th>
            <v-th sortKey="deaths" defaultSort="desc">Смертей</v-th>
            <v-th sortKey="kdRatio" defaultSort="desc">K/D</v-th>
            <v-th sortKey="totalScore" defaultSort="desc">Счет</v-th>
          </thead>
          <tbody slot="body" slot-scope="{ displayData }">
            <tr v-for="row in displayData" :key="row.id">
              <td>
                <router-link
                  :to="{ name: 'Player', params: { nick: row.playerName } }"
                  >{{ row.lastSquadPrefix + row.playerName }}</router-link
                >
              </td>
              <td>{{ row.totalPlayedGames }}</td>
              <td>{{ row.kills }}</td>
              <td>{{ row.teamkills }}</td>
              <td>{{ row.deaths }}</td>
              <td>{{ row.kdRatio }}</td>
              <td>{{ row.totalScore }}</td>
            </tr>
          </tbody>
        </v-table>
        <p>Статистка по неделям:</p>
        <div v-if="this.byWeeks.length < 1">Игр не найдено.</div>
        <v-table :data="byWeeks" v-else class="responsive-table highlight">
          <thead slot="head">
            <th>Даты</th>
            <v-th sortKey="totalPlayedGames">Кол-во игр</v-th>
            <v-th sortKey="kills">Убийств</v-th>
            <v-th sortKey="teamkills">Тимкиллов</v-th>
            <v-th sortKey="deaths">Смертей</v-th>
            <v-th sortKey="kdRatio">K/D</v-th>
            <v-th sortKey="totalScore">Счет</v-th>
          </thead>
          <tbody slot="body" slot-scope="{ displayData }">
            <tr v-for="row in displayData" :key="row.id">
              <td>{{ row.startDate }} - {{ row.endDate }}</td>
              <td>{{ row.totalPlayedGames }}</td>
              <td>{{ row.kills }}</td>
              <td>{{ row.teamkills }}</td>
              <td>{{ row.deaths }}</td>
              <td>{{ row.kdRatio }}</td>
              <td>{{ row.score }}</td>
            </tr>
          </tbody>
        </v-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Player",
  data: () => ({
    n: [],
    byWeeks: [],
    loading: true,
    choosed: "За все время",
    period: [],
  }),
  async mounted() {
    let r = await fetch(`rotations_stats.json?t=${Date.now()}`);
    r = await r.json();
    for (let key in r) {
      let tStartDate = r[key].startDate;
      let tEndDate = r[key].endDate;
      r[key].startDate = new Date(r[key].startDate).toLocaleString("ru", {
        year: "numeric",
        month: "short",
        day: "numeric",
      });
      if (r[key].endDate === null) {
        r[key].endDate = " по настоящий момент.";
      } else {
        r[key].endDate = new Date(r[key].endDate).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
      }
      this.period.push({
        id: Number(key),
        title: `${r[key].startDate} - ${r[key].endDate}`,
        endDate: tEndDate,
        startDate: tStartDate,
      });
    }
    this.period.push({
      id: r.length,
      title: "За все время",
    });
    this.period.reverse();
    await this.refresh();
  },
  watch: {
    choosed: function () {
      this.n = [];
      this.byWeeks = [];
      this.refresh();
    },
  },
  methods: {
    async refresh() {
      if (this.choosed == this.period[0].title) {
        this.r();
      } else {
        this.rotationTable();
      }
    },
    async r() {
      this.loading = true;
      this.n = [];
      this.byWeeks = [];
      let r = await fetch(`stats.json?t=${Date.now()}`);
      r = await r.json();
      for (let key in r.globalStatistics) {
        if (r.globalStatistics[key].playerName === this.$route.params.nick) {
          if (r.globalStatistics[key].lastSquadPrefix === null) {
            r.globalStatistics[key].lastSquadPrefix = "";
          }
          this.n.push(r.globalStatistics[key]);
          this.byWeeks = r.globalStatistics[key].byWeeks;
        }
      }
      for (let key in this.byWeeks) {
        this.byWeeks[key].totalScore = (
          this.byWeeks[key].kills -
          this.byWeeks[key].teamkills / this.byWeeks[key].totalPlayedGames
        ).toFixed(2);
        this.byWeeks[key].startDate = new Date(
          this.byWeeks[key].startDate
        ).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
        this.byWeeks[key].endDate = new Date(
          this.byWeeks[key].endDate
        ).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
      }
      this.loading = false;
    },
    async rotationTable() {
      this.loading = true;
      this.n = [];
      this.byWeeks = [];
      let r = await fetch(`rotations_stats.json?t=${Date.now()}`);
      r = await r.json();
      let id = 1;
      let rotationID = this.$refs.sl.selectedIndex;
      for (let key in r) {
        if (r[key].startDate == this.period[rotationID].startDate) {
          for (let key1 in r[key].stats.global) {
            if (
              r[key].stats.global[key1].playerName == this.$route.params.nick
            ) {
              this.n.push(r[key].stats.global[key1]);
              this.byWeeks = r[key].stats.global[key1].byWeeks;
            }
          }
        }
      }
      for (let key in this.byWeeks) {
        this.byWeeks[key].startDate = new Date(
          this.byWeeks[key].startDate
        ).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
        this.byWeeks[key].endDate = new Date(
          this.byWeeks[key].endDate
        ).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
      }
      this.loading = false;
    },
  },
};
</script>
